body {
    background-color: #ffcc00;
    padding: 20px;
    margin: 0;

    h1, h2, p, ul, li{
        font-family: sans-serif;
    }

    .header{
        background-color: #111;
        padding: 0;

        li {
            display: inline;
            list-style-type: none;
            margin: 0;
        }

        a{
            color: #fff;
            font-weight: bold;
            text-decoration: none;
            padding: 20px;
            display: inline-block;
        }
    }

    .content {
        background-color: #fff;
        padding: 20px;

        h2{
            padding: 0;
            margin: 0;
        }

        li {
            margin-bottom: 10px;
        }
    }
    .active{
        background-color: #0099ff;
    }
}